/*Rate*/

.rate-popup {
    .vote_rating_box {
        padding: 0;
        margin-bottom: 0;
        border: none;
        text-align: center;
        .btn_vne {
            width: 140px;
            font-size: 17px;
        }
    }
    .rating {
        justify-content: center;
        margin-bottom: 25px;
        width: 100%;
    }
    textarea {
        height: 96px;
        border: 1px solid #e5e5e5;
        border-radius: 3px;
        font-size: 15px;
        line-height: 1.6;
        font-family: arial;
        padding: 15px;
        resize: none;
        color: #222;
    }
}

.vote_rating_box {
    width: 100%;
    float: left;
    text-align: left;
    background: #ffffff;
    margin-bottom: 15px;
    .item_row_bx {
        margin-bottom: 15px;
    }
    .title-box-vote {
        font-size: 14px;
        margin-bottom: 10px;
        color: #757575;
        line-height: 16px;
        float: left;
    }
    .title_bx {
        font-size: 20px;
        margin-bottom: 15px;
    }
    .label_check {
        font-size: 18px;
        line-height: 21px;
        .radio_check {
            top: 4px;
        }
    }
    .span_bh {
        padding-left: 0 !important;
        padding-top: 10px;
        float: left;
    }
}

.rating {
    margin-bottom: 15px;
    justify-content: flex-start;
    width: auto;
    float: left;
}

.item-point {
    min-width: 155px;
    margin-right: 35px;
    .row-number {
        align-items: center;
    }
    .star-point {
        margin-right: 10px;
        .ic {
            width: 40px;
            height: 40px;
            fill: #f2c94c;
        }
    }
    .number-star {
        font-size: 18px;
        color: #757575;
        .point-bold {
            font-size: 24px;
            font-weight: 700;
            color: #222;
        }
        .point-small {
            line-height: 26px;
        }
        span {
            display: inline-block;
            vertical-align: bottom;
        }
    }
    .txt-point {
        font-size: 16px;
        margin-top: 10px;
    }
    &.point-your {
        margin-right: 0;
        cursor: pointer;
        .star-point {
            .ic {
                fill: #e5e5e5;
            }
        }
        .number-star {
            .point-bold {
                color: #757575;
            }
        }
        &.your-rate {
            .star-point {
                .ic {
                    fill: #0887e2;
                }
            }
            .number-star {
                .point-bold {
                    color: #222;
                }
            }
        }
    }
}

.title-rate-pop {
    font: 700 18px/160%;
    text-align: left;
    margin-bottom: 15px !important;
}

.block_one_start {
    width: 40px;
    height: 40px;
    font-size: 0;
    margin: 0 20px 0 0;
    .star {
        display: inline-block;
        width: 20px;
        overflow: hidden;
        .ic {
            fill: #e5e5e5;
            width: 40px;
            height: 40px;
        }
        &.star-right {
            .ic {
                margin-left: -20px;
            }
        }
    }
    .star.hover,
    .star:hover {
        .ic {
            fill: #0887e2;
        }
    }
}


