.circle-loading {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    height: 50px;
    width: 50px;
    margin: -25px 0 0 -25px;
    border: 4px rgba(159, 34, 78, 0.25) solid;
    border-top: 4px rgb(159, 34, 78) solid;
    border-bottom: 4px rgb(159, 34, 78) solid;
    border-radius: 50%;
    -webkit-animation: spin1 1s infinite linear;
    animation: spin1 1s infinite linear;
}

@-webkit-keyframes spin1 {
    from {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(359deg);
        transform: rotate(359deg);
    }
}

@keyframes spin1 {
    from {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(359deg);
        transform: rotate(359deg);
        -webkit-transform: rotate(359deg);
        transform: rotate(359deg);
    }
}

.wrap-video-lightbox {
    cursor: pointer;
    .embed-container {
        margin-bottom: 0;
    }
    .thumb-video {
        background: #000;
    }
    .error-mess {
        font-size: 15px;
        line-height: 160%;
        color: #fff;
        width: 100%;
        padding: 0 15px;
        text-align: center;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
}


