/*send-scheme-popup*/

.send-scheme-popup {
    .title-form {
        font-size: 16px;
        margin-bottom: 15px;
    }
    .university__filter {
        .form-group--branch {
            width: 100%;
            height: 40px;
            margin-bottom: 20px;
            border: 1px solid #e5e5e5;
            border-radius: 4px;
            display: flex;
            align-items: center;
            padding: 0 10px;
            overflow: hidden;
            .text-input {
                display: flex;
                white-space: nowrap;
                color: #616161;
                font-size: 14px;
                margin-left: 7px;
                span {
                    color: #9F224E;
                    margin-left: 3px;
                }
            }
            i {
                position: static;
                transform: none;
                color: #757575;
            }
            .form-control {
                border: none;
                background: none;
                padding-left: 10px;
                &:focus {
                    border: none;
                    box-shadow: none;
                }
            }
            input[type="file"] {
                opacity: 0;
                width: 100%;
                height: 100%;
                position: absolute;
                left: 0;
                top: 0;
                border: none;
                z-index: 1;
                cursor: pointer;
            }
            .btn-upload {
                width: 100px;
                height: 32px;
                display: flex;
                align-items: center;
                justify-content: center;
                color: #8C99B4;
                font-size: 13px;
                font-weight: 700;
                position: absolute;
                right: 10px;
                top: 50%;
                transform: translateY(-50%);
                background: #F5F7FA;
                box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.15);
                border-radius: 6px;
                i {
                    color: #C4CCDE;
                    margin-left: 16px;
                }
            }
        }
    }
}


