.end-meeting {
    background: #292C3D;
    padding: 18px 24px;
    max-width: 366px;
    .tit {
        font-weight: 500;
        font-size: 18px;
        line-height: 160%;
        color: rgba(255, 255, 255, 0.85);
        margin-bottom: 18px;
    }
    .txt-info {
        font-size: 14px;
        line-height: 160%;
        color: rgba(255, 255, 255, 0.65);
        margin-bottom: 16px;
    }
    .group-button {
        display: flex;
        justify-content: flex-end;
        .btn {
            margin-left: 8px;
            border-radius: 2px;
            color: var(--color-main);
            display: block;
            width: 80px;
            height: 32px;
            &.rejoin {
                background: #0E78F9;
                border: 1px solid #0E78F9
            }
            &.cancel {
                border: 1px solid #747681
            }
        }
    }
    .mfp-close {
        background: transparent;
        border: 1px solid transparent;
        right: 15px;
        top: 10px;
        font-size: 32px;
        color: #515A6E;
        &:hover {
            cursor: pointer;
        }
    }
