.group-main-button {
    padding: 110px 0;
    position: fixed;
    bottom: -175px;
    left: 50%;
    width: 100%;
    max-width: 600px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all .3s ease;
    transform: translateX(-50%);
    @media (max-width: 767px) {
        border-top: 1px solid rgba(255, 255, 255, 0.3);
        padding: 10px 0;
        bottom: 0;
        background: var(--bg-themes);
        max-width: 100%;
    }
    &:hover {
        @media (min-width: 768px) {
            bottom: -90px;
            padding-top: 30px;
            @media (max-width: 767px) {
                bottom: 0;
                padding-top: 10px;
            }
            .show-hide {
                top: 100%;
            }
        }
    }
    &.active {
        bottom: 40px;
        @media (max-width: 767px) {
            bottom: 30px;
        }
    }
    .item {
        min-width: 48px;
        height: 48px;
        border-radius: 100%;
        background: #2F3346;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .2s ease;
        position: relative;
        &:hover,
        &.active {
            background: #0E78F9;
            cursor: pointer;
            &.voice {
                background: #2F3346;
            }
        }
        &:not(:last-child) {
            margin-right: 24px;
        }
        &.voice {
            &:hover {
                background: #2F3346;
                /* .icon-Voice {
                    color: #FF334B;
                    &:before {
                        content: '\e900';
                    }
                } */
            }
        }
        &.video {
            &:hover {
                background: #2F3346;
                /* .icon-Video {
                    color: #FF334B;
                    &:before {
                        content: '\e908';
                    }
                } */
                [class*="icon-"] {
                    background: #2F3346;
                }
            }
        }
        &.share-screen {
            @media (max-width: 767px) {
                display: none;
            }
        }
        &.chat {
            @media (max-width: 767px) {
                display: none;
            }
            &.notify {
                /*&:before {
                    content: '';
                    width: 15px;
                    height: 15px;
                    background: #1C1F2D;
                    z-index: 1;
                    position: absolute;
                    right: 0;
                    top: 0;
                    border-radius: 100%;
                }*/
                &:after {
                    content: '';
                    width: 10px;
                    height: 10px;
                    background: #EE0000;
                    z-index: 1;
                    position: absolute;
                    right: 2px;
                    top: 2px;
                    border-radius: 100%;
                }
            }
        }
        &.settings {
            @media (max-width: 767px) {
                display: none;
            }
            &.active {
                .box-setting {
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
        &.seemore {
            &.active {
                .box-seemore {
                    opacity: 1;
                    visibility: visible;
                }
            }
            .box-seemore {
                position: absolute;
                right: 0;
                bottom: 52px;
                background: #292C3D;
                border-radius: 6px;
                padding: 20px 20px 35px 20px;
                min-width: 198px;
                width: 100%;
                transition: all .2s ease;
                opacity: 0;
                visibility: hidden;
                li {
                    display: flex;
                    align-items: center;
                    font-size: 16px;
                    line-height: 160%;
                    color: rgba(255, 255, 255, 0.85);
                    padding-left: 40px;
                    position: relative;
                    &:not(:last-child) {
                        padding-bottom: 20px;
                    }
                    &.share-screen,
                    &.chat,
                    &.setting {
                        &:before {
                            content: '\e906';
                            font-family: 'icomoon' !important;
                            font-style: normal;
                            font-weight: normal;
                            font-variant: normal;
                            text-transform: none;
                            line-height: 1;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                            position: absolute;
                            left: 0;
                            top: 0;
                            font-size: 24px;
                        }
                    }
                    &.chat {
                        &:before {
                            content: '\e901';
                        }
                    }
                    &.setting {
                        &:before {
                            content: '\e905';
                        }
                    }
                }
            }
        }
        [class*="icon-"] {
            color: var(--color-main);
            font-size: 20px;
        }
    }
    .end-meeting {
        background: #FF334B;
        color: var(--color-main);
        border-radius: 6px;
        text-align: center;
        font-weight: 500;
        font-size: 14px;
        padding: 14px 16px;
        white-space: nowrap;
        @media (max-width: 767px) {
            width: 48px;
            height: 48px;
            border-radius: 100%;
        }
        &:hover {
            cursor: pointer;
        }
        span {
            @media (max-width: 767px) {
                display: none;
            }
        }
        [class*="icon-"] {
            display: none;
            font-size: 18px;
            color: var(--color-main);
            @media (max-width: 767px) {
                display: block;
            }
        }
    }
}


/*Box Setting*/

.box-setting {
    background: #292c3d;
    border-radius: 2px;
    color: var(--color-main);
    min-width: 366px;
    padding: 12px 24px;
    width: 100%;
    position: absolute;
    bottom: 60px;
    right: 0;
    opacity: 0;
    visibility: hidden;
    transition: all .2s ease;
    &:after {
        content: '';
        width: 100%;
        height: 12px;
        background: transparent;
        position: absolute;
        left: 0;
        bottom: -12px;
    }
    &__head {
        margin-bottom: 8px;
        .title {
            font-size: 16px;
            line-height: 160%;
        }
    }
    &__content {
        .list-input {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-row: 10px;
            margin-bottom: 16px;
            li {
                font-weight: 400;
                font-size: 14px;
                line-height: 160%;
                color: rgba(255, 255, 255, .85);
                margin-bottom: 10px;
            }
            .checkbox-custom {
                position: relative;
                line-height: 20px;
                padding-left: 28px;
                display: block;
                input {
                    position: absolute;
                    opacity: 0;
                    cursor: pointer;
                    height: 0;
                    width: 0;
                    &:checked {
                        &~.checkmark {
                            background-color: #2196f3;
                            border: 1px solid #2196f3;
                            &:after {
                                display: block;
                            }
                        }
                    }
                }
                &:hover {
                    input {
                        &~.checkmark {
                            border: 1px solid #2196f3;
                        }
                    }
                }
                .checkmark {
                    &:after {
                        border: solid #fff;
                        border-width: 0 2px 2px 0;
                        height: 10px;
                        left: 6px;
                        top: 1px;
                        -webkit-transform: rotate(45deg);
                        transform: rotate(45deg);
                        width: 5px;
                    }
                }
            }
        }
        .box-dropdown {
            &__inner {
                background: #787A83;
                border: 1px solid #787A83;
                border-radius: 2px;
                margin-bottom: 8px;
                padding: 8px 16px;
                color: rgba(255, 255, 255, 0.85);
                position: relative;
                width: 100%;
                &:after {
                    content: '';
                    width: 5px;
                    height: 5px;
                    border-right: 1px solid rgba(255, 255, 255, 0.85);
                    border-bottom: 1px solid rgba(255, 255, 255, 0.85);
                    transform: rotate(45deg);
                    display: block;
                    position: absolute;
                    right: 12px;
                    top: 50%;
                    transform: translateY(-50%) rotate(45deg);
                }
                .list-item {
                    background: #FFFFFF;
                    box-shadow: 0px 4px 25px rgba(81, 90, 110, 0.15);
                    border-radius: 2px;
                    position: absolute;
                    left: 0;
                    top: 42px;
                    width: 100%;
                    z-index: 1;
                    li {
                        padding: 10px 16px;
                        &.active {
                            a {
                                color: #0068E6;
                            }
                        }
                        a {
                            color: #30394A;
                            font-size: 14px;
                            line-height: 160%;
                        }
                    }
                }
            }
        }
    }
    .close {
        background: #0e78f9;
        border-radius: 2px;
        color: var(--color-main);
        width: 80px;
        height: 32px;
        font-size: 14px;
        line-height: 160%;
        float: right;
        &:hover {
            cursor: pointer;
        }
    }
}

.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    border: 1px solid #787A83;
    border-radius: 6px;
    &:after {
        content: "";
        position: absolute;
        display: none;
    }
}


/*Icon Show Hỉde*/

.show-hide {
    display: flex;
    align-items: center;
    background: rgba(47, 51, 70, .6);
    border-radius: 100%;
    color: var(--color-main);
    font-size: 16px;
    width: 40px;
    height: 40px;
    justify-content: center;
    position: fixed;
    left: 50%;
    top: 54px;
    transform: translateX(-50%);
    transition: all .3s ease;
    @media (max-width: 767px) {
        display: none;
    }
    &:hover {
        background: #2f3346;
    }
    .icon-arrow {
        transform: rotate(-90deg);
    }
    &.active {
        /* @media (max-width: 767px) {
            bottom: -100%;
        } */
        .icon-arrow {
            transform: rotate(90deg);
        }
    }
}