* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-rendering: optimizeLegibility;
}


/* Scrollbar custom Works on Firefox */

* {
    scrollbar-width: thin;
    scrollbar-color: rgb(192, 192, 192) white;
}


/* Works on Chrome, Edge, and Safari */

*::-webkit-scrollbar {
    width: 6px;
}

*::-webkit-scrollbar-track {
    background: #1c1f2d;
}

*::-webkit-scrollbar-thumb {
    background-color: #323542;
    border-radius: 20px;
    border: 3px solid #323542;
}

img,
svg,
iframe,
audio,
video {
    max-width: 100%;
}

a {
    text-decoration: none;
    color: inherit;
}

ul,
ol {
    list-style: none;
}

button {
    border: 0;
    background: transparent;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

textarea,
input[type=text],
input[type=number],
input[type=password] {
    appearance: none;
    &:focus {
        outline: none;
        border: 1px solid #e5e5e5;
    }
}

// Reset Vne Default
.section {
    float: none;
}

[class*="icon"] {
    color: var(--color-primary);
    transition: all .2s ease;
}

.icon-Videooff,
.icon-Voiceoff {
    color: #FF334B !important;
}

input[type=email],
input[type=password],
input[type=tel],
input[type=text],
select,
textarea {
    border: 1px solid #E5E5E5;
    border-radius: 4px;
}


/*flexbox*/

.flexbox {
    display: flex;
}

.flex-wrap {
    flex-wrap: wrap;
}