html {
    -webkit-text-size-adjust: none;
    touch-action: manipulation;
}

body {
    background: var(--bg-themes);
    font-size: 16px;
    color: var(--color-main);
    line-height: 1.4;
    font-family: var(--font-main);
    counter-reset: mySecCounter;
    /* height: 100vh; */
    &.fixed {
        overflow: hidden;
    }
    &.swal2-height-auto {
        height: 100vh !important;
    }
}