    left: -4px; }

.arrow-tooltip {
  position: absolute;
  width: 8px;
  height: 8px;
  background: inherit;
  visibility: hidden; }
  .arrow-tooltip::before {
    position: absolute;
    width: 8px;
    height: 8px;
    background: inherit;
    visibility: visible;
    content: '';
    transform: rotate(45deg); }

