.top-header {
    padding: 8px 50px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.15);
    @media (max-width: 767px) {
        padding: 8px 15px;
    }
    .container {
        display: flex;
        align-items: center;
        position: relative;
    }
    .info-room {
        display: flex;
        align-items: center;
        @media (max-width: 767px) {
            flex-wrap: wrap;
            justify-content: center;
            width: 100%;
            text-align: center;
        }
        .box-type-room {
            display: flex;
            align-items: center;
            @media (max-width: 767px) {
                display: block;
                flex-wrap: wrap;
                justify-content: center;
            }
        }
        .user {
            font-size: 14px;
            margin-right: 24px;
            position: relative;
            display: flex;
            align-items: center;
            @media (max-width: 767px) {
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
            }
            &:hover {
                cursor: pointer;
            }
            &.active {
                .box-list-person {
                    opacity: 1;
                    visibility: visible;
                }
            }
            .icon-user {
                font-size: 22px;
            }
            .person {
                margin-left: 8px;
                @media (max-width: 767px) {
                    margin-left: 4px;
                }
                span {
                    @media (max-width: 767px) {
                        display: none;
                    }
                }
            }
            .box-list-person {
                position: absolute;
                left: 0;
                top: 39px;
                padding: 18px 24px;
                min-width: 366px;
                width: 100%;
                background: #292C3D;
                box-shadow: 0px 3px 6px -4px rgba(0, 0, 0, 0.12), 0px 6px 16px rgba(0, 0, 0, 0.08), 0px 9px 28px 8px rgba(0, 0, 0, 0.05);
                border-radius: 2px;
                z-index: 1;
                opacity: 0;
                visibility: hidden;
                transition: all .2s ease;
                @media (max-width: 767px) {
                    width: 100%;
                    max-width: 366px;
                    min-width: auto;
                }
                .title {
                    margin-bottom: 18px;
                    font-weight: 500;
                    font-size: 18px;
                    line-height: 160%;
                }
                .item {
                    display: flex;
                    align-items: center;
                    &:not(:last-child) {
                        margin-bottom: 16px;
                    }
                    .box-info-user {
                        width: 40px;
                    }
                    .name {
                        margin-left: 16px;
                    }
                }
            }
        }
        .type-room {
            font-size: 14px;
            line-height: 20px;
            margin-right: 24px;
            @media (max-width: 767px) {
                margin-right: 0;
            }
        }
        .time {
            background: rgba(98, 106, 147, 0.5);
            border-radius: 2px;
            padding: 4px 10px;
            line-height: 18px;
            @media (max-width: 767px) {
                background: transparent;
                padding: 0;
            }
            span {
                position: relative;
                padding-left: 16px;
                font-size: 14px;
                @media (max-width: 767px) {
                    padding-left: 0;
                }
                &:before {
                    content: '';
                    width: 7px;
                    height: 7px;
                    background-color: #00BC4B;
                    border-radius: 100%;
                    position: absolute;
                    left: 0;
                    top: 5px;
                    @media (max-width: 767px) {
                        content: none;
                    }
                }
            }
        }
    }
    .header-option__right {
        display: flex;
        align-items: center;
        margin-left: auto;
        @media (max-width: 767px) {
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
        }
        .share-link {
            font-size: 16px;
            line-height: normal;
            span {
                @media (max-width: 767px) {
                    display: none;
                }
            }
        }
    }
    .group-button {
        display: flex;
        .list-frame {
            padding: 11px;
            width: 40px;
            height: 40px;
            background-color: #2F3346;
            color: #414762;
            display: flex;
            align-items: center;
            justify-items: center;
            justify-content: center;
            margin-left: 16px;
            border-radius: 100%;
            font-size: 1.375rem;
            @media (max-width: 360px) {
                margin-left: 8px;
            }
            &:hover,
            &.active {
                background-color: var(--hover-main);
                [class*="icon-"] {
                    color: var(--color-main);
                }
            }
            &.list {
                order: 1;
                @media (max-width: 767px) {
                    order: 2;
                }
            }
            &.main {
                order: 2;
                @media (max-width: 767px) {
                    order: 1;
                }
                .icon-layout2 {
                    @media (max-width: 767px) {
                        display: none;
                    }
                }
                .icon-layout-mobile {
                    display: none;
                    @media (max-width: 767px) {
                        display: block;
                    }
                }
            }
            [class*="icon-"] {
                color: #414762;
            }
        }
    }
}