.page-login {
    background: url(../img/bg_login.jpg) no-repeat;
    background-position: center 0;
    background-size: cover;
    height: 100vh;
    .container {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        @media (max-width: 767px) {
            padding: 0 15px;
        }
    }
    .box-login {
        width: 100%;
        max-width: 600px;
        margin: 0 auto;
        padding: 32px 48px;
        background: #2A2E42;
        border-radius: 6px;
        @media (max-width: 767px) {
            padding: 16px;
            background: rgba(42, 46, 66, .5);
        }
        .box-title {
            margin-bottom: 18px;
            .title {
                font-weight: 700;
                font-size: 40px;
                line-height: 120%;
                color: rgba(255, 255, 255, 0.9);
                @media (max-width: 767px) {
                    font-size: 32px;
                }
            }
            .text-message {
                font-size: 16px;
                line-height: 160%;
                color: #9da0ab;
                @media (max-width: 767px) {
                    font-size: 14px;
                }
            }
        }
        .room {
            margin-top: 18px;
            color: #ddd;
            font-size: 18px;
            margin-bottom: 12px;
        }
        .form {
            margin-bottom: 18px;
            .form-control {
                margin-bottom: 16px;
                position: relative;
                &.upload {
                    display: flex;
                    align-items: center;
                    .box-info-user {
                        width: 56px;
                        .box-avatar {
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            border-radius: 6px;
                            background: rgba(65, 71, 93, 0.8);
                            img {
                                border-radius: 6px;
                                width: 100%;
                                height: 100%;
                                object-fit: cover;
                                position: absolute;
                                top: 50%;
                                left: 50%;
                                transform: translate(-50%, -50%);
                            }
                            [class*="icon-"] {
                                font-size: 22px;
                                color: #9da0ab;
                                position: absolute;
                                top: 50%;
                                left: 50%;
                                transform: translate(-50%, -50%);
                            }
                        }
                    }
                    input {
                        width: auto;
                    }
                }
                &.name {
                    &::before {
                        content: "\e913";
                        position: absolute;
                        left: 14px;
                        top: 50%;
                        transform: translateY(-50%);
                        font-size: 20px;
                        font-family: 'icomoon' !important;
                        speak: never;
                        font-style: normal;
                        font-weight: normal;
                        font-variant: normal;
                        text-transform: none;
                        line-height: 1;
                        /* Better Font Rendering =========== */
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                        color: rgba(255, 255, 255, 0.6);
                    }
                }
                &.room {
                    @extend .name;
                    &::before {
                        content: "\e910";
                    }
                }
                &.passcode {
                    @extend .name;
                    &::before {
                        content: "\e920";
                    }
                }
                input {
                    background: rgba(65, 71, 93, 0.8);
                    border-radius: 6px;
                    width: 100%;
                    border: 1px solid rgba(65, 71, 93, 0.8);
                    padding: 18px 14px 18px 46px;
                    color: var(--color-main);
                    font-size: 16px;
                    &::placeholder {
                        color: #9da0ab;
                    }
                    @media (max-width: 767px) {
                        padding: 14px 14px 14px 46px;
                    }
                }
            }
        }
        .group-button {
            display: flex;
            justify-content: center;
            margin-bottom: 24px;
            .item {
                text-align: center;
                font-size: 14px;
                &:not(:last-child) {
                    margin-right: 32px;
                }
            }
            .icon {
                width: 56px;
                height: 56px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                background: #3D4358;
                margin: 0 auto 8px auto;
                border-radius: 100%;
                ;
                [class*="icon-"] {
                    color: var(--color-main);
                    font-size: 24px;
                }
            }
        }
        .join-meeting {
            width: 100%;
            max-width: 137px;
            margin: 0 auto;
            background: #0E78F9;
            color: #FFFFFF;
            border-radius: 6px;
            padding: 15px 22px;
            font-size: 16px;
            line-height: 160%;
            display: flex;
            justify-content: center;
            white-space: nowrap;
            &:hover {
                cursor: pointer;
            }
        }
        .back-login {
            text-align: center;
            width: 100%;
            float: left;
            margin: 12px 0 0;
            text-decoration: underline;
            font-size: 14px;
            color: #9da0ab;
        }
    }
}


/*Upload File Custom*/

.box-upload-file {
    display: flex;
    .inputfile {
        width: 0.1px;
        height: 0.1px;
        opacity: 0;
        overflow: hidden;
        position: absolute;
        z-index: -1;
    }
    label {
        font-size: 14px;
        text-overflow: ellipsis;
        white-space: nowrap;
        cursor: pointer;
        display: inline-block;
        overflow: hidden;
        padding: 0.395rem 1rem;
        color: #0E78F9;
        width: 100%;
        display: flex;
        align-items: center;
        @media (max-width: 767px) {
            color: rgba(255, 255, 255, 0.85);
        }
        * {
            pointer-events: none;
        }
        span {
            margin-right: 8px;
            [class*="icon"] {
                font-size: 20px;
            }
        }
        [class*="icon"] {
            font-size: 14px;
        }
    }
}