                        /*Box Avatar*/
                        
                        .box-info-user {
                            .box-avatar {
                                border-radius: 50%;
                                height: 0;
                                overflow: hidden;
                                padding-bottom: 100%;
                                position: relative;
                                width: 100%;
                                img {
                                    bottom: 0;
                                    height: 100%;
                                    left: 0;
                                    -o-object-fit: cover;
                                    object-fit: cover;
                                    position: absolute;
                                    right: 0;
                                    top: 0;
                                    width: 100%;
                                }
                            }
                        }
                        
                        .page-video-call {
                            padding: 0 110px;
                            height: calc(100vh - 130px);
                            display: flex;
                            align-items: center;
                            @media (max-width: 1366px) {
                                padding: 0 40px;
                            }
                            @media (max-width: 1023px) {
                                height: auto;
                                padding: 15px 15px 0 15px;
                            }
                            .box-video {
                                /*overflow: hidden;
                                height: 0;
                                position: relative;
                                width: 100%;
                                padding-bottom: 56.25%;*/
                                video {
                                    height: 100%;
                                    left: 50%;
                                    position: absolute;
                                    top: 50%;
                                    transform: translate(-50%, -50%);
                                    width: 100%;
                                }
                            }
                            &.list-frame {
                                padding-top: 10px;
                                .container {
                                    height: 100%;
                                }
                                .wrap-video {
                                    display: flex;
                                    position: relative;
                                    @media (max-width: 767px) {
                                        flex-wrap: wrap;
                                    }
                                    &.full-screen {
                                        .box-video {
                                            &-call {
                                                &__inner {
                                                    .box-info-user {
                                                        @media (max-width: 767px) {
                                                            width: 24px;
                                                        }
                                                    }
                                                    .group-button {
                                                        @media (max-width: 767px) {
                                                            bottom: 8px;
                                                            right: 8px;
                                                            top: auto;
                                                        }
                                                        .item {
                                                            @media (max-width: 767px) {
                                                                background: transparent;
                                                            }
                                                        }
                                                    }
                                                }
                                            }
                                            &__inner {
                                                /*@media (max-width: 767px) {
                                                    padding-bottom: calc(177.78% - 56px);
                                                }*/
                                            }
                                            &__outer {
                                                @media (max-width: 767px) {
                                                    /* margin: -10px -15px 0 -15px;
                                                    width: calc(100% + 30px); */
                                                }
                                            }
                                            &__outer-scroll {
                                                @media (max-width: 767px) {
                                                    position: fixed !important;
                                                    bottom: 68px;
                                                    left: 15px;
                                                    top: auto;
                                                }
                                                &.show-group-button {
                                                    @media (max-width: 767px) {
                                                        bottom: 84px;
                                                    }
                                                }
                                            }
                                            &.list {
                                                @media (max-width: 767px) {
                                                    display: flex;
                                                    overflow: auto;
                                                    grid-gap: 0 8px;
                                                }
                                            }
                                            .box-video-call {
                                                @media (max-width: 767px) {
                                                    min-width: 104px;
                                                    margin: 0;
                                                }
                                            }
                                        }
                                    }
                                    &.collapse {
                                        .expand-collapse {
                                            [class*="icon-"] {
                                                transform: rotate(0deg);
                                            }
                                        }
                                    }
                                    .expand-collapse {
                                        position: absolute;
                                        top: 50%;
                                        transform: translateY(-50%);
                                        right: -68px;
                                        background: rgba(47, 51, 70, .6);
                                        font-size: 20px;
                                        color: var(--color-main);
                                        width: 40px;
                                        height: 40px;
                                        border-radius: 100%;
                                        transition: all .3s ease;
                                        display: flex;
                                        align-items: center;
                                        justify-content: center;
                                        &:hover {
                                            background: #2f3346;
                                        }
                                        [class*="icon-"] {
                                            font-size: 16px;
                                            transform: rotate(-180deg);
                                        }
                                        @media (max-width: 767px) {
                                            display: none;
                                        }
                                    }
                                    .box-video {
                                        width: calc(100% - 260px);
                                        @media (max-width: 767px) {
                                            width: 100%;
                                        }
                                        &__outer-scroll {
                                            width: 228px;
                                            overflow: hidden;
                                            position: absolute;
                                            right: 0;
                                            top: 0;
                                            transition: all .3s ease;
                                            @media (max-width: 767px) {
                                                width: 100%;
                                            }
                                            @media (max-width: 767px) {
                                                position: static !important;
                                                margin: 0;
                                            }
                                            &.scrollbar-outer {
                                                >.scroll-element {
                                                    &.scroll-y {
                                                        opacity: 0;
                                                        visibility: hidden;
                                                    }
                                                }
                                            }
                                            &.collapse {
                                                width: 80px;
                                                background: #292C3D;
                                                border-radius: 48px;
                                                padding: 14px 12px;
                                                .name {
                                                    display: none;
                                                }
                                                .group-button {
                                                    display: none;
                                                }
                                                .box-video {
                                                    &-call {
                                                        background: transparent;
                                                        width: 56px;
                                                        margin: 0 auto 8px auto;
                                                        &__inner {
                                                            padding-bottom: 100%;
                                                            .box-info-user {
                                                                top: 50%;
                                                                width: 100%;
                                                            }
                                                        }
                                                        &.show-video {
                                                            border-radius: 100%;
                                                            border: 1px solid transparent;
                                                            .box-video {
                                                                display: none;
                                                            }
                                                            .box-info-user {
                                                                display: block;
                                                            }
                                                        }
                                                    }
                                                    &.list {
                                                        max-height: calc(100vh - 175px);
                                                    }
                                                    .box-total-user {
                                                        position: sticky;
                                                        bottom: 0;
                                                        &:before {
                                                            content: "";
                                                            background: #292c3d;
                                                            bottom: -15px;
                                                            height: 80px;
                                                            left: 0;
                                                            position: absolute;
                                                            width: 56px;
                                                            z-index: -1;
                                                        }
                                                        span {
                                                            background: rgba(255, 255, 255, 0.2);
                                                            border-radius: 100%;
                                                            width: 56px;
                                                            height: 56px;
                                                            display: flex;
                                                            align-items: center;
                                                            justify-content: center;
                                                            font-size: 14px;
                                                            line-height: 160%;
                                                        }
                                                    }
                                                }
                                                .box-voice {
                                                    display: none;
                                                }
                                            }
                                        }
                                        &.list {
                                            width: auto;
                                            max-height: calc(100vh - 128px);
                                            transition: all .3s ease;
                                            @media (max-width: 767px) {
                                                display: grid;
                                                grid-gap: 0 16px;
                                                grid-template-columns: repeat(2, 1fr);
                                            }
                                            &:hover {
                                                cursor: pointer;
                                            }
                                            .box-voice {
                                                zoom: .1;
                                            }
                                        }
                                        &-call {
                                            width: 100%;
                                            margin-left: 0;
                                            &:last-child {
                                                margin-bottom: 0;
                                            }
                                            &__inner {
                                                .box-info-user {
                                                    top: 45%;
                                                    width: 40px;
                                                    @media (max-width: 767px) {
                                                        top: 42%;
                                                    }
                                                    .box-avatar {
                                                        [class*="icon-"] {
                                                            font-size: 16px;
                                                        }
                                                    }
                                                }
                                                .box-pin,
                                                .full-screen {
                                                    display: none;
                                                }
                                                .name {
                                                    font-size: 12px;
                                                    bottom: 10px;
                                                    left: 10px;
                                                    @media (max-width: 767px) {
                                                        bottom: 8px;
                                                        overflow: hidden;
                                                        text-overflow: ellipsis;
                                                        white-space: nowrap;
                                                        width: 42px;
                                                    }
                                                }
                                                .group-button {
                                                    top: 8px;
                                                    right: 12px;
                                                    bottom: auto;
                                                    @media (max-width: 767px) {
                                                        top: 6px;
                                                        right: 8px;
                                                    }
                                                    .item {
                                                        width: 20px;
                                                        height: 20px;
                                                        font-size: 12px;
                                                        @media (max-width: 767px) {
                                                            background: transparent !important;
                                                        }
                                                        &.ic-video {
                                                            display: none;
                                                        }
                                                    }
                                                }
                                            }
                                        }
                                        &.host {
                                            display: flex;
                                            flex-wrap: wrap;
                                            align-items: center;
                                            justify-content: center;
                                            margin-left: -8px;
                                            margin-top: -8px;
                                            overflow: auto;
                                            height: calc(100vh - 90px);
                                            @media (max-width: 767px) {
                                                margin: 0 0 16px 0;
                                                height: auto;
                                            }
                                            &.multi-screen {
                                                @media (max-width: 767px) {
                                                    height: auto;
                                                }
                                                .box-video__outer {
                                                    width: calc(50% - 8px);
                                                    margin-left: 8px;
                                                    position: relative;
                                                    @media (max-width: 767px) {
                                                        width: 100%;
                                                        margin: 0 0 8px 0;
                                                    }
                                                }
                                            }
                                            .box-voice {
                                                position: absolute;
                                                top: 80px;
                                                left: 80px;
                                            }
                                        }
                                        &__outer {
                                            width: 100%;
                                            margin-top: 8px;
                                            position: relative;
                                            border-radius: 6px;
                                            @media (max-width: 767px) {
                                                margin-top: 0;
                                            }
                                            .ic-maximize {
                                                position: absolute;
                                                right: 20px;
                                                top: 20px;
                                                @media (max-width: 767px) {
                                                    right: 12px;
                                                    top: 12px;
                                                }
                                                [class*="icon-"] {
                                                    font-size: 24px;
                                                    @media (max-width: 767px) {
                                                        font-size: 20px;
                                                    }
                                                }
                                            }
                                            &:hover {
                                                cursor: pointer;
                                                .user-share-tooltip {
                                                    opacity: 1;
                                                    visibility: visible;
                                                }
                                            }
                                            .user-share-tooltip {
                                                position: absolute;
                                                top: 0;
                                                left: 50%;
                                                transform: translateX(-50%);
                                                background: rgba(28, 31, 45, .7);
                                                border-radius: 3px;
                                                padding: 6px 16px;
                                                white-space: nowrap;
                                                font-size: 14px;
                                                line-height: 20px;
                                                opacity: 0;
                                                visibility: hidden;
                                                transition: all .3s ease;
                                            }
                                        }
                                        &__inner {
                                            height: 0;
                                            padding-bottom: 56.25%;
                                            position: relative;
                                            width: 100%;
                                        }
                                        img {
                                            position: absolute;
                                            left: 0;
                                            top: 0;
                                            right: 0;
                                            bottom: 0;
                                            object-fit: cover;
                                        }
                                    }
                                }
                            }
                            .container {
                                width: 100%;
                                margin: 0 auto;
                                position: relative;
                                .wrap-box-frame {
                                    display: flex;
                                    @media (max-width: 767px) {
                                        flex-wrap: wrap;
                                    }
                                    &__inner {
                                        width: 100%;
                                        display: flex;
                                        flex-wrap: wrap;
                                        align-items: center;
                                        transition: all .3s ease;
                                        @media (max-width: 767px) {
                                            height: calc(100vh - 141px);
                                            overflow: auto;
                                        }
                                        &.show-chat {
                                            padding-right: 206px;
                                        }
                                    }
                                    .box-frame {
                                        display: flex;
                                        flex-wrap: wrap;
                                        width: calc(100% + 16px);
                                        margin-left: -16px;
                                        &.single-frame {
                                            .box-video-call {
                                                max-width: 1200px;
                                                margin: 0 auto;
                                                &__inner {
                                                    >.name {
                                                        display: none;
                                                    }
                                                    .box-info-user {
                                                        .box-avatar {
                                                            width: 176px;
                                                            height: 176px;
                                                            [class*="icon-"] {
                                                                font-size: 70px;
                                                            }
                                                        }
                                                        .name {
                                                            display: block;
                                                        }
                                                    }
                                                }
                                            }
                                        }
                                        &.full-frame {
                                            justify-content: center;
                                            .box-video-call {
                                                flex-grow: 0;
                                                @media (max-width: 1023px) {
                                                    width: calc(50% - 16px);
                                                }
                                                &__inner {
                                                    .box-info-user {
                                                        width: 64px;
                                                        @media (max-width: 767px) {
                                                            top: 40%;
                                                            width: 40px;
                                                        }
                                                    }
                                                    .box-avatar {
                                                        &.default {
                                                            [class*="icon-"] {
                                                                font-size: 26px;
                                                                @media (max-width: 767px) {
                                                                    font-size: 16px;
                                                                }
                                                            }
                                                        }
                                                    }
                                                    .icon-pin {
                                                        top: 16px;
                                                        left: 16px;
                                                        @media (max-width: 767px) {
                                                            top: 12px;
                                                            left: 12px;
                                                        }
                                                    }
                                                    .full-screen {
                                                        right: 16px;
                                                        top: 16px;
                                                        @media (max-width: 767px) {
                                                            right: 8px;
                                                            top: 6px;
                                                        }
                                                    }
                                                    .group-button {
                                                        right: 16px;
                                                        bottom: 16px;
                                                        @media (max-width: 767px) {
                                                            right: 0;
                                                            bottom: 4px;
                                                        }
                                                        .item {
                                                            @media (max-width: 767px) {
                                                                background: transparent;
                                                                margin-left: 0;
                                                            }
                                                        }
                                                    }
                                                    .name {
                                                        left: 16px;
                                                        bottom: 16px;
                                                        overflow: hidden;
                                                        width: 200px;
                                                        text-overflow: ellipsis;
                                                        white-space: nowrap;
                                                        @media (max-width: 1366px) {
                                                            width: 82px;
                                                            left: 10px;
                                                            bottom: 8px;
                                                        }
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }
                                .box-video-call {
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;
                                    background: rgba(41, 44, 61, .65);
                                    border-radius: 6px;
                                    width: calc(25% - 16px);
                                    margin-left: 16px;
                                    margin-bottom: 16px;
                                    flex-grow: 1;
                                    transition: all .2s ease;
                                    @media (max-width: 1366px) {
                                        width: calc(33.33% - 16px);
                                    }
                                    @media (max-width: 1023px) {
                                        width: calc(50% - 16px);
                                    }
                                    @media (max-width: 767px) {
                                        width: 100%;
                                    }
                                    &:hover {
                                        cursor: pointer;
                                        .icon-pin {
                                            opacity: 1;
                                            visibility: visible;
                                        }
                                    }
                                    &.show-video {
                                        border: 1px solid #0E78F9;
                                        border-radius: 3px;
                                        .box-video-call {
                                            &__inner {
                                                .box-info-user,
                                                .name {
                                                    display: none;
                                                }
                                            }
                                        }
                                        .group-button {
                                            .ic-voice {
                                                background: rgba(57, 62, 86, .3);
                                            }
                                        }
                                    }
                                    &__inner {
                                        width: 100%;
                                        height: 0;
                                        padding-bottom: 56.25%;
                                        position: relative;
                                        &.active {
                                            border: 2px solid #0E78F9;
                                        }
                                        .box-voice {
                                            zoom: .1;
                                            top: 68px;
                                            left: 68px;
                                            position: absolute;
                                        }
                                        .icon-pin {
                                            background: #393E56;
                                            width: 32px;
                                            height: 32px;
                                            border-radius: 100%;
                                            display: flex;
                                            align-items: center;
                                            justify-content: center;
                                            position: absolute;
                                            top: 24px;
                                            left: 24px;
                                            font-size: 16px;
                                            color: var(--color-main);
                                            opacity: 0;
                                            visibility: hidden;
                                        }
                                        .full-screen {
                                            position: absolute;
                                            right: 24px;
                                            top: 24px;
                                            font-size: 16px;
                                            color: var(--color-main);
                                            @media (max-width: 767px) {
                                                right: 12px;
                                                top: 12px;
                                            }
                                        }
                                        .box-video {
                                            display: flex;
                                            width: 100%;
                                        }
                                        .group-button {
                                            position: absolute;
                                            right: 24px;
                                            bottom: 24px;
                                            display: flex;
                                            @media (max-width: 767px) {
                                                right: 12px;
                                                bottom: 12px;
                                            }
                                            .item {
                                                width: 32px;
                                                height: 32px;
                                                background: #393E56;
                                                font-size: 16px;
                                                display: flex;
                                                align-items: center;
                                                justify-content: center;
                                                margin-left: 12px;
                                                border-radius: 100%;
                                                [class*="icon-"] {
                                                    color: var(--color-main);
                                                }
                                                &.ic-voice {
                                                    &:hover,
                                                    &.active {
                                                        .icon-Voice {
                                                            color: #FF334B;
                                                            &:before {
                                                                content: '\e900';
                                                            }
                                                        }
                                                    }
                                                }
                                                &.ic-video {
                                                    &:hover,
                                                    &.active {
                                                        .icon-Video {
                                                            color: #FF334B;
                                                            &:before {
                                                                content: '\e908';
                                                            }
                                                        }
                                                    }
                                                }
                                            }
                                        }
                                        .box-info-user {
                                            position: absolute;
                                            transform: translate(-50%, -50%);
                                            left: 50%;
                                            top: 50%;
                                            width: 96px;
                                            .box-avatar {
                                                display: flex;
                                                align-items: center;
                                                justify-content: center;
                                                border-radius: 50%;
                                                overflow: hidden;
                                                background-color: rgba(57, 62, 86, .65);
                                                margin: 0 auto;
                                                padding-bottom: 100%;
                                                &.default {
                                                    [class*="icon-"] {
                                                        display: block;
                                                    }
                                                }
                                                [class*="icon-"] {
                                                    font-size: 40px;
                                                    position: absolute;
                                                    left: 50%;
                                                    top: 50%;
                                                    transform: translate(-50%, -50%);
                                                    display: none;
                                                }
                                                img {
                                                    position: absolute;
                                                    left: 50%;
                                                    top: 50%;
                                                    transform: translate(-50%, -50%);
                                                    border-radius: 50%;
                                                    object-fit: cover;
                                                    width: 100%;
                                                    height: 100%;
                                                }
                                            }
                                            .name {
                                                position: static;
                                                display: none;
                                                margin-top: 16px;
                                                font-size: 14px;
                                            }
                                        }
                                        .name {
                                            position: absolute;
                                            bottom: 24px;
                                            left: 24px;
                                            font-weight: 700;
                                            font-size: 14px;
                                            line-height: 160%;
                                            @media (max-width: 767px) {
                                                left: 12px;
                                                bottom: 12px;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                        /*Box Chat*/
                        
                        .box-chat {
                            position: fixed;
                            right: 0;
                            bottom: 0;
                            width: 100%;
                            max-width: 300px;
                            padding: 8px 8px 16px 8px;
                            background: #292C3D;
                            border-radius: 6px;
                            margin-left: 16px;
                            flex-direction: column;
                            opacity: 0;
                            visibility: hidden;
                            transition: all .3s ease;
                            z-index: 1;
                            display: none;
                            @media (max-width: 767px) {
                                position: fixed;
                                top: 72px;
                                left: 0;
                                bottom: 0;
                                right: 0;
                                max-width: 100%;
                                margin: 0;
                            }
                            &.active {
                                opacity: 1;
                                visibility: visible;
                                display: flex;
                            }
                            &__title {
                                display: flex;
                                align-items: center;
                                font-weight: 500;
                                font-size: 16px;
                                .icon-close {
                                    margin-left: auto;
                                    color: #515A6E;
                                    font-size: 26px;
                                    &:hover {
                                        cursor: pointer;
                                    }
                                }
                            }
                            &__body {
                                margin-top: 30px;
                                overflow: auto;
                                height: calc(100vh - 180px);
                                @media (max-width: 767px) {
                                    overflow: auto;
                                }
                            }
                            &__inner {
                                &.wrap-mess {
                                    margin-bottom: 16px;
                                    &.reply {
                                        .avatar {
                                            display: none;
                                        }
                                        .box-messenger {
                                            justify-content: flex-end;
                                            margin-right: 8px;
                                            width: 100%;
                                            .messenger {
                                                background: #0E78F9;
                                                color: rgba(255, 255, 255, .7);
                                                border-radius: 6px 6px 1px 6px;
                                            }
                                        }
                                        .user-seen {
                                            width: 16px;
                                            height: 16px;
                                            border-radius: 100%;
                                            background: #0E78F9;
                                            display: flex;
                                            align-items: center;
                                            justify-content: center;
                                            font-size: 6px;
                                        }
                                    }
                                }
                                &.head {
                                    display: flex;
                                    padding-left: 40px;
                                    .nickname {
                                        color: #707176;
                                        font-size: 12px;
                                    }
                                    .time {
                                        margin-left: auto;
                                        color: #707176;
                                        font-size: 12px;
                                        line-height: 160%;
                                    }
                                }
                                &.content {
                                    display: flex;
                                    align-items: flex-end;
                                    .avatar {
                                        .box-info-user {
                                            width: 32px;
                                        }
                                    }
                                    .box-messenger {
                                        display: flex;
                                        flex-wrap: wrap;
                                        .messenger {
                                            font-size: 14px;
                                            line-height: 160%;
                                            max-width: 185px;
                                            margin-top: 4px;
                                            margin-left: 8px;
                                            padding: 6px 6px 6px 12px;
                                            background: #434961;
                                            border-radius: 6px 6px 6px 1px;
                                            color: rgba(217, 211, 211, .7);
                                            position: relative;
                                            width: 100%;
                                            &:hover {
                                                .feedback {
                                                    opacity: 1;
                                                    visibility: visible;
                                                }
                                            }
                                            .feedback {
                                                position: absolute;
                                                top: 50%;
                                                transform: translateY(-50%);
                                                left: calc(100% + 6px);
                                                display: flex;
                                                opacity: 0;
                                                visibility: hidden;
                                                transition: all .3s ease;
                                                li {
                                                    &:not(:last-child) {
                                                        margin-right: 4px;
                                                    }
                                                    a {
                                                        color: #707176;
                                                    }
                                                }
                                            }
                                        }
                                    }
                                    .user-seen {
                                        margin-left: auto;
                                        display: flex;
                                        .box-info-user {
                                            width: 16px;
                                            margin-left: 4px;
                                        }
                                    }
                                    .box-avatar {
                                        padding-bottom: 100%;
                                        overflow: hidden;
                                        height: 0;
                                        position: relative;
                                        width: 100%;
                                        border-radius: 50%;
                                        &.default {
                                            background-color: rgba(57, 62, 86, .65);
                                            [class*="icon-"] {
                                                position: absolute;
                                                top: 50%;
                                                left: 50%;
                                                transform: translate(-50%, -50%);
                                            }
                                        }
                                        img {
                                            object-fit: cover;
                                            position: absolute;
                                            top: 0;
                                            bottom: 0;
                                            left: 0;
                                            right: 0;
                                            width: 100%;
                                            height: 100%;
                                        }
                                    }
                                }
                            }
                            &__footer {
                                margin-top: auto;
                                .box-type {
                                    position: relative;
                                    .text-typing {
                                        font-weight: 500;
                                        font-size: 10px;
                                        margin-bottom: 6px;
                                        position: absolute;
                                        bottom: 100%;
                                        left: 0;
                                        width: 100%;
                                        @media (max-width: 767px) {
                                            margin-top: 6px;
                                        }
                                    }
                                    .frame-type {
                                        position: relative;
                                        input {
                                            -webkit-box-align: center;
                                            -ms-flex-align: center;
                                            align-items: center;
                                            background: #212331;
                                            border-radius: 6px;
                                            color: hsla(0, 0%, 100%, .4);
                                            display: -webkit-box;
                                            display: -ms-flexbox;
                                            display: flex;
                                            font-size: 14px;
                                            line-height: 160%;
                                            padding: 16px 10px;
                                            width: 100%;
                                            border: 1px solid #212331;
                                        }
                                        .ic-group {
                                            position: absolute;
                                            top: 16px;
                                            right: 10px;
                                            display: flex;
                                            align-items: center;
                                            [class*="icon-"] {
                                                font-size: 24px;
                                            }
                                            .icon-emoji {
                                                color: #707176;
                                            }
                                            .icon-send {
                                                color: #0E78F9;
                                            }
                                            .send-messenger,
                                            .ic-emoji {
                                                margin-left: 8px;
                                                &:hover {
                                                    cursor: pointer;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                        }