    .box-voice__inner {
        width: 100%;
        height: 100%;
        position: relative;
        transform: translateZ(0) scale(1);
        backface-visibility: hidden;
        transform-origin: 0 0;
        div {
            position: absolute;
            width: 10px;
            box-sizing: content-box;
            &:nth-child(1) {
                left: 15px;
                background: #0e78f9;
                animation: box-loading__inner-1 1s cubic-bezier(0, 0.5, 0.5, 1) infinite;
                animation-delay: -0.3s;
            }
            &:nth-child(2) {
                left: 55px;
                background: #0e78f9;
                animation: box-loading__inner-2 1s cubic-bezier(0, 0.5, 0.5, 1) infinite;
                animation-delay: -0.1s;
            }
            &:nth-child(3) {
                left: 95px;
                background: #0e78f9;
                animation: box-loading__inner-3 1s cubic-bezier(0, 0.5, 0.5, 1) infinite;
            }
            &:nth-child(4) {
                left: 135px;
                background: #0e78f9;
                animation: box-loading__inner-4 1s cubic-bezier(0, 0.5, 0.5, 1) infinite;
            }
            &:nth-child(5) {
                left: 175px;
                background: #0e78f9;
                animation: box-loading__inner-5 1s cubic-bezier(0, 0.5, 0.5, 1) infinite;
                animation-delay: -0.2s;
            }
        }
    }
    
    .box-voice {
        width: 200px;
        height: 200px;
        display: inline-block;
        overflow: hidden;
        zoom: .15;
        @media (max-width: 767px) {
            zoom: .1;
        }
    }
    /*Keyframe*/
    
    @keyframes box-loading__inner-1 {
        0% {
            top: 48px;
            height: 80px
        }
        50% {
            top: 80px;
            height: 40px
        }
        100% {
            top: 80px;
            height: 40px
        }
    }
    
    @keyframes box-loading__inner-2 {
        0% {
            top: 48px;
            height: 104px
        }
        50% {
            top: 60px;
            height: 80px
        }
        100% {
            top: 60px;
            height: 80px
        }
    }
    
    @keyframes box-loading__inner-3 {
        0% {
            top: 41.99999999999999px;
            height: 116.00000000000001px
        }
        50% {
            top: 20px;
            height: 150px;
        }
        100% {
            top: 60px;
            height: 80px
        }
    }
    
    @keyframes box-loading__inner-4 {
        0% {
            top: 48px;
            height: 104px
        }
        50% {
            top: 60px;
            height: 80px
        }
        100% {
            top: 60px;
            height: 80px
        }
    }
    
    @keyframes box-loading__inner-5 {
        0% {
            top: 48px;
            height: 80px
        }
        50% {
            top: 80px;
            height: 40px
        }
        100% {
            top: 80px;
            height: 40px
        }
    }
    /*Poper JS*/
    
    .tooltip {
        background: #292C3D;
        color: white;
        font-weight: 400;
        padding: 4px 8px;
        font-size: 14px;
        line-height: 24px;
        border-radius: 2px;
        padding: 8px;
        display: none;
        &[data-show] {
            display: block;
        }
        &[data-popper-placement^='top']>#arrow {
            bottom: -4px;
        }
        &[data-popper-placement^='bottom']>#arrow {
            top: -4px;
        }
        &[data-popper-placement^='left']>#arrow {
            right: -4px;
        }
        &[data-popper-placement^='right']>#arrow {
            left: -4px;
        }
    }
    
    .arrow-tooltip {
        position: absolute;
        width: 8px;
        height: 8px;
        background: inherit;
        &::before {
            position: absolute;
            width: 8px;
            height: 8px;
            background: inherit;
            visibility: visible;
            content: '';
            transform: rotate(45deg);
        }
        visibility: hidden;
    }
    
    //sweetalert2
    .swal2-popup {
        background: #292C3D;
        box-shadow: 0px 3px 6px -4px rgba(0, 0, 0, 0.12), 0px 6px 16px rgba(0, 0, 0, 0.08), 0px 9px 28px 8px rgba(0, 0, 0, 0.05);
        border-radius: 2px;
        color: var(--color-main);
        .swal2-close {
            color: #515A6E;
        }
        .swal2-title {
            font-weight: 500;
            font-size: 18px;
            line-height: 160%;
            text-align: left;
            color: rgba(255, 255, 255, 0.65);
        }
        .swal2-html-container {
            color: rgba(255, 255, 255, 0.65);
            font-size: 14px;
            line-height: 160%;
            text-align: left;
        }
        .swal2-actions {
            padding: 0 1em;
            margin-right: 0;
            .swal2-styled {
                font-size: 14px;
                line-height: 160%;
                border-radius: 2px;
                padding: 0.345em 1.1em;
            }
            .swal2-cancel {
                border: 1px solid #747681;
                color: rgba(255, 255, 255, 0.65);
            }
        }
    }